.footer {
  padding: 60px 0;
  color: $footer-primary-color;
  background-color: $black-colour;

  &__title {
    color: $footer-primary-color;
  }

  &__subtitle {
    display: flex;
    margin-bottom: 16px;
    font-weight: 500;
    line-height: 100%;
    letter-spacing: 0.02em;
    text-transform: uppercase;
    color: $subtitle-color;
  }

  &__list {
    list-style: none;
    margin: 0;
    padding: 0;
  }

  &__item {
    margin-bottom: 16px;
    text-decoration: none;

    &:last-child {
      margin: 0;
    }
  }

  &__link {
    color: $footer-primary-color;
    text-decoration: none;
  }

  &__social-section {
    display: flex;
  }

  &__social {
    display: flex;
    gap: 38px;
  }

  &__social-link {
    display: flex;
  }

  &__social-icon {
    height: 100%;
    fill: $orange-color;

    &:hover {
      fill: $footer-primary-color;
    }
  }

  &__copy {
    color: $subtitle-color;
  }

  &__logo-wraper {
    margin-bottom: 44px;
  }
}
